import { mapState, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      textVal: '',
      oJson: '',
      initialStatus: false
    }
  },
  onShareAppMessage () {
    const data = {
      shareid: this.userInfo ? this.userInfo.user_id : '',
      channel: ''
    }
    return {
      title: '瓶盖思维',
      path: `/pages/index/main?json=${JSON.stringify(data)}`
    }
  },
  onLoad (options) {
    this.oJson = JSON.parse(decodeURIComponent(options.oJson))
    this.textVal = this.oJson
    this.initialStatus = false
  },
  onShow () {
    if (!this.oJson) this.emptyDataFn()
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'isMyDataInfo'
    ]),
    // 保存
    onSaveFn () {
      if (this.initialStatus) return false
      const data = {
        that: this,
        field: 'salary',
        infoVal: this.textVal
      }
      if (!this.textVal) {
        this.showToastFn('薪资不能为空!')
        return false
      }
      uni.showLoading({ title: 'Loading' })
      this.initialStatus = true
      this.isMyDataInfo(data).then(() => {
        if (this.oJson) {
          this.showToastFn('修改成功')
        } else {
          this.showToastFn('添加成功')
        }
        setTimeout(() => {
          uni.navigateBack()
        }, 1500)
        uni.hideLoading()
      })
    },
    // 清空数据
    emptyDataFn () {
      this.textVal = ''
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 1500
      })
    }
  },
  watch: {
    oJson (str) {
      if (str) {
        this.textVal = str
      } else {
        this.emptyDataFn()
      }
    }
  },
  onUnload () {
    this.emptyDataFn()
  }
}
